import React, { Component } from 'react';
import Projects from './Projects';
import SocialProfiles from './SocialProfiles';
import Title from './Title';
import bb from '../assets/bb.png';
import WorkExperience from "./workExperience";

class App extends Component {
  state = { displayBio: false };

  toggleDisplayBio = () => {
    this.setState({ displayBio: !this.state.displayBio });
  }

  render() {
    return (
      <div>
        <img src={bb} alt='profile' className='profile' />
        <h1>Hello!</h1>
        <p>My name is Giacomo.</p>
        <Title />
        <p>I'm always looking forward to working on meaningful projects.</p>
        {
          this.state.displayBio ? (
            <div>
              <p>I'm Italian living in London, and code every day.</p>
              <p>My favorite language is JavaScript, but i also know Java and c based languages.</p>
              <p>Besides coding, I also love movies and art!</p>
              <button onClick={this.toggleDisplayBio}>Show less</button>
            </div>
          ) : (
            <div>
              <button onClick={this.toggleDisplayBio}>Read more</button>
            </div>
          )
        }
        <hr />
        <Projects />
        <hr />
        <WorkExperience />
        <hr />
        <SocialProfiles />
      </div>
    )
  }
}

export default App;
